nav{
	min-height:10vh;
	display:flex;
	justify-content:space-around;
	align-items:center;
	button{
		background:trasparent;
		border:none;
		cursor: pointer;
		border:2px solid rgb(65,65,65);
		padding:0.5rem;
		transition:all 0.3s ease;
		&:hover{
			background:rgb(65,65,65);
			color:#fff;
		}
	}
}

@media screen and(max-width:768){
	nav{
		button{
			z-index: 10;
		}
	}	
}

